<template>
  <div class="container">
    <loading :active.sync="isLoading"></loading>
    <nav aria-label="breadcrumb">
      <ol class="breadcrumb bg-beige border-bottom">
        <li class="breadcrumb-item">
          <router-link to="/" class="text-lightgrey"
        style="text-decoration: none;"></router-link>
        </li>
        <li class="breadcrumb-item">
          <router-link to="/products" class="text-lightgrey"
        style="text-decoration: none;"></router-link>
        </li>
        <li class="breadcrumb-item font-weight-bold text-coffee2 active" aria-current="page">
          {{ product.title }}
        </li>
      </ol>
    </nav>
    <div class="mt-4">
      <div class="">
        <div class="row align-items-center">
          <div class="col-md-6">
            <img :src="product.imageUrl" class="img-fluid" alt />
          </div>
          <div class="col-md-6">
            <div class="">
              <blockquote>
                <div class="d-flex justify-content-around align-items-center">
                  <h3 class="text-coffee2 font-weight-bold ">{{ product.title }}</h3>
                  <span class="badge badge-pill badge-coffee2">{{ product.category }}</span>
                </div>
                <div class="px-3">
                  <p class="pt-3 text-coffee2 h5">{{ product.content }}</p>
                  <span>{{ product.description }}</span>
                </div>
                <div class="d-flex justify-content-around text-coffee2 mt-4">
                  <div
                    class="h6 product-origin"
                    v-if="!product.price"
                  >NT ${{ product.origin_price | thousand }} </div>
                  <div
                    class="h5 font-weight-bold ml-auto"
                    v-if="product.price"
                  >NT ${{ product.price | thousand }} </div>
                </div>
              </blockquote>

              <select v-model="product.num" class="form-control mt-3">
                <option value="0" disabled selected></option>
                <option
                  v-for="productNum in 10"
                  :key="productNum"
                  :value="productNum"
                > {{ productNum }} {{ product.unit }}</option>
              </select>
              <div class="mt-5 d-flex justify-content-end align-items-center">
                <div class="mr-3 product-off h5 pt-2" v-if="product.num">
                  
                  <strong class="product-off">
                    ${{ product.price * product.num | thousand }} </strong>
                </div>
                <button
                  type="button"
                  class="btn btn-coffee2"
                  @click="detailAddToCart(product,product.num)"
                  v-if="product.num" style="width: 50%"
                ></button>
                <button type="button" class="btn btn-coffee2" style="width: 50%"
                v-else disabled></button>
              </div>
            </div>
          </div>
        </div>

      </div>
    </div>
        <div class="row mt-5 pt-4 justify-content-center border-top"
        v-if="!(product.category === 'Other')">
            <div class="col-md-6">
              <div class="mb-5">
              <h4 class="text-coffee2 mb-3 pb-3 text-center"></h4>
              <p>
                {{ product.title }}<br><br>
                <br><br>
                {{ product.category }}<br><br>
                <br><br>
                365 ()<br><br>
                100 <br><br>
                454g  10g<br><br>
                
              </p>
              </div>
            </div>
            <div class="col-md-6">
              <div class="mb-5">
              <h4 class="text-coffee2 pb-3"></h4>
              <p>
                 10g  150 ~ 200cc <br><br><br>
                 90 ~ 92 
              </p>
              </div>
            </div>
        </div>
        <div class="row pt-5 justify-content-center">
            <div class="col-md-12">
              <div class="mb-5">
              <h4 class="text-coffee2 mb-3 pb-3 font-weight-bold"></h4>
              <Swiper></Swiper>
              </div>
            </div>
        </div>
    <Toast></Toast>
    <Toasterror></Toasterror>
  </div>
</template>

<script>
/* global $ */
import Swiper from '@/components/Swiper.vue';
import Toast from '../../components/Toast.vue';
import Toasterror from '../../components/Toasterror.vue';

export default {
  components: {
    Swiper,
    Toast,
    Toasterror,
  },
  data() {
    return {
      product: {},
      isLoading: false,
    };
  },
  methods: {
    getProduct() {
      this.isLoading = true;
      const { id } = this.$route.params;
      const getProductApi = `${process.env.VUE_APP_PATH}api/${process.env.VUE_APP_UUID}/ec/product/${id}`;
      this.$http.get(getProductApi).then((res) => {
        this.product = res.data.data;
        this.$set(this.product, 'num', 0);
        this.isLoading = false;
      });
    },
    detailAddToCart(item, quantity) {
      const addToCartUrl = `${process.env.VUE_APP_PATH}api/${process.env.VUE_APP_UUID}/ec/shopping`;
      const cart = {
        product: item.id,
        quantity,
      };
      this.isLoading = true;
      this.$http
        .post(addToCartUrl, cart)
        .then(() => {
          this.$bus.$emit('get-cart');
          $('.toastSuc').toast('show');
          this.isLoading = false;
        })
        .catch(() => {
          $('.toastErr').toast('show');
          this.isLoading = false;
        });
    },
  },
  created() {
    this.getProduct();
  },
};
</script>

<style>
body{
  background-color: #fefbf4;
}
